<template>
  <div @click="Reset_Timer()">
    <div id="sub" @click="onClick" @contextmenu.prevent="onRightClick">{{data}}</div>
  </div>
</template>

<script>
import testMixin from '@/mixins/testMixin'
import timer from '@/mixins/timer'
export default {
  mixins: [testMixin, timer],
  name: "testMixin",
  data() {
    return {
      data: 10 + this.weight
    };
  },
  created(){
    console.log('component');
    console.log(this.data)
  },
  methods: {
    onRightClick(){
      this.data--;
    }
  }
};
</script>

<style>
#sub {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  width: 100px;
  height: 100px;
  background-color: skyblue;
}
</style>
