let timer = {
    data() {
        return {
            count_Timer: 0,
            reset_Timer: 15
        }
    },
    mounted () {
        this.Start_Timer()
    },
    beforeDestroy () {
        clearInterval(this.interval_Timer)
    },
    watch: {
        count_Timer(value) {
            if(value === this.reset_Timer){
                clearInterval(this.interval_Timer)
                this.$router.push('/1_Leaflet')
            }
        }
    },
    methods:{
        Start_Timer() {
            this.interval_Timer = setInterval(() => {
                this.count_Timer ++
                console.log('count',this.count_Timer)
            }, 1000)
        },
        Reset_Timer(){
            this.count_Timer = 0
        }
    }
};
export default timer;